import React from "react";
import "./top-division-c.css";
import MytypedComponent from "../typing-effect/typing.js";

import {TOP_SECTION, SOCIALS} from "../../Module/General";

function Btn(props) {
  return (
    <button className={props.class}>
      <i className={props.ico}></i>
      {props.type}
      <div className="overlay">
        <div className="overlay-text">{props.overlay}</div>
      </div>
    </button>
  );
}

function About() {
  return (
    <div className="AboutMe">
      <h2>{TOP_SECTION.TITLE}</h2>
      <MytypedComponent />
    </div>
  );
}

function Myinfo() {
  return (
    <div className="Myinfo">
      <About />
      <p> {TOP_SECTION.SHORT_DESCRIPTION}</p>
     <br></br>
      {/* This is judges application button */}
      <a href={TOP_SECTION.JUDGES_FORM_LINK}>
        {" "}
        <Btn class="sponsor_btn" type="Discord" overlay="Join our Channel " />
      </a>

          {/*  <a href={TOP_SECTION.HACKERS_REGISTRATION_FORM_LINK}>
              {" "}
              <script defer async src="https://apply.devfolio.co/v2/sdk.js"></script>

        <Btn class="register" type="Devfolio" overlay="Apply With Devfolio" />
      </a> */}
          {/*    <a><div
             class="apply-button"
              data-hackathon-slug="amritathon"
              data-button-theme="light"
              style="height: 44px; width: 312px"
          ></div> </a> */}
    </div>
  );
}

export {Btn, Myinfo};
